"use client";

import React, {
  useState,
  useCallback,
  useMemo,
  useEffect,
  Fragment,
} from "react";
import Link from "next/link";
import {
  DocumentMagnifyingGlassIcon,
  CloudArrowUpIcon,
  ArrowPathIcon,
  ArrowRightIcon,
  ExclamationTriangleIcon,
  ClockIcon,
  ChartBarIcon,
  DocumentTextIcon,
  AdjustmentsHorizontalIcon,
  XMarkIcon,
  FunnelIcon,
} from "@heroicons/react/24/outline";

/* ================================
   TYPES
================================ */

type CompareResult = {
  doc_name: string;
  v1_label: string;
  v2_label: string;
  pages_v1: number;
  pages_v2: number;
  paragraphs_v1: number;
  paragraphs_v2: number;
  changes_count: number;
  risk_level: string;
  summary_text: string;
  json_report_path: string;
  html_report_path: string;
  run_id: number;
  html_report_url: string;
  json_report_url: string;
};

type ChangeType = "ADDED" | "REMOVED" | "MODIFIED";

type ChangeItem = {
  change_type: ChangeType;
  section_label: string | null;
  old_text: string | null;
  new_text: string | null;
  risk_level?: "LOW" | "MEDIUM" | "HIGH" | null;
  ai_comment?: string | null;
};

type JsonReport = {
  document_name: string;
  version_old: string;
  version_new: string;
  overall_risk_level: string | null;
  summary_text: string | null;
  changes: ChangeItem[];
};

type FilterType = "ALL" | ChangeType;

/* ================================
   CONSTANTS / CONFIG
================================ */

const API_BASE = "http://127.0.0.1:8000";

const INITIAL_LABELS = {
  docName: "TestDoc",
  v1: "v1",
  v2: "v2",
};

const EMPTY_ARRAY: ChangeItem[] = [];

/* ================================
   UTILS
================================ */

const cn = (...cls: (string | false | null | undefined)[]) =>
  cls.filter(Boolean).join(" ");

const truncate = (text: string | null, len = 260) => {
  if (!text) return "";
  return text.length > len ? text.slice(0, len) + "…" : text;
};

const riskColor = (risk: string) => {
  const lv = risk.toLowerCase();
  if (lv.includes("high") || lv.includes("สูง"))
    return "bg-red-100 text-red-700 border-red-200";
  if (lv.includes("medium") || lv.includes("กลาง"))
    return "bg-amber-100 text-amber-700 border-amber-200";
  return "bg-emerald-100 text-emerald-700 border-emerald-200";
};

/* ================================
   BADGES
================================ */

function RiskBadge({ level }: { level?: string | null }) {
  const base =
    "inline-flex items-center px-2 py-0.5 rounded-full text-[11px] font-semibold border";

  const lv = (level || "LOW").toUpperCase();

  if (lv === "HIGH")
    return (
      <span className={`${base} bg-red-100 text-red-700 border-red-200`}>
        HIGH
      </span>
    );

  if (lv === "MEDIUM")
    return (
      <span className={`${base} bg-amber-100 text-amber-700 border-amber-200`}>
        MEDIUM
      </span>
    );

  return (
    <span className={`${base} bg-emerald-100 text-emerald-700 border-emerald-200`}>
      LOW
    </span>
  );
}

function ChangeTypeBadge({ type }: { type: ChangeType }) {
  const base =
    "inline-flex items-center gap-1 px-2 py-0.5 rounded-full text-[11px] font-semibold";

  if (type === "ADDED")
    return (
      <span className={`${base} bg-emerald-100 text-emerald-700`}>
        ＋ ADDED
      </span>
    );

  if (type === "REMOVED")
    return (
      <span className={`${base} bg-rose-100 text-rose-700`}>− REMOVED</span>
    );

  return (
    <span className={`${base} bg-amber-100 text-amber-700`}>
      <AdjustmentsHorizontalIcon className="h-3 w-3" />
      MODIFIED
    </span>
  );
}

/* ================================
   MAIN PAGE
================================ */

export default function Page() {
  /* ---------- form state ---------- */
  const [docName, setDocName] = useState(INITIAL_LABELS.docName);
  const [v1Label, setV1Label] = useState(INITIAL_LABELS.v1);
  const [v2Label, setV2Label] = useState(INITIAL_LABELS.v2);
  const [fileV1, setFileV1] = useState<File | null>(null);
  const [fileV2, setFileV2] = useState<File | null>(null);

  /* ---------- ui state ---------- */
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  /* ---------- data ---------- */
  const [result, setResult] = useState<CompareResult | null>(null);
  const [changes, setChanges] = useState<ChangeItem[]>(EMPTY_ARRAY);

  /* ---------- filter ---------- */
  const [filter, setFilter] = useState<FilterType>("ALL");
  const [showFilter, setShowFilter] = useState(true);

  /* ================================
     DERIVED
  ================================ */

  const filteredChanges = useMemo(() => {
    if (filter === "ALL") return changes;
    return changes.filter((c) => c.change_type === filter);
  }, [changes, filter]);

  const hasResult = !!result;
  const hasChanges = filteredChanges.length > 0;

  /* ================================
     ACTIONS
  ================================ */

  const resetResult = useCallback(() => {
    setResult(null);
    setChanges(EMPTY_ARRAY);
    setError(null);
  }, []);

  const handleSubmit = useCallback(
    async (e: React.FormEvent) => {
      e.preventDefault();
      resetResult();

      if (!fileV1 || !fileV2) {
        setError("กรุณาเลือกไฟล์ PDF ทั้งสองเวอร์ชัน");
        return;
      }

      const formData = new FormData();
      formData.append("doc_name", docName);
      formData.append("v1_label", v1Label);
      formData.append("v2_label", v2Label);
      formData.append("file_v1", fileV1);
      formData.append("file_v2", fileV2);

      setLoading(true);

      try {
        const res = await fetch(`${API_BASE}/compare`, {
          method: "POST",
          body: formData,
        });

        if (!res.ok) {
          const j = await res.json().catch(() => ({}));
          throw new Error(j.error || `API error (${res.status})`);
        }

        const data: CompareResult = await res.json();
        setResult(data);

        const jsonRes = await fetch(`${API_BASE}${data.json_report_url}`);
        if (jsonRes.ok) {
          const json: JsonReport = await jsonRes.json();
          setChanges(json.changes || EMPTY_ARRAY);
        }
      } catch (err: any) {
        setError(err.message || "ไม่สามารถประมวลผลได้");
      } finally {
        setLoading(false);
      }
    },
    [docName, v1Label, v2Label, fileV1, fileV2, resetResult]
  );

  /* ================================
     RENDER (PART 2 ต่อ)
  ================================ */

  return (
    <main className="min-h-screen bg-gradient-to-br from-[#f5e9ff] via-[#f7f0ff] to-[#e3d4ff] px-4 py-8">
      {/* PART 2 จะเริ่มจาก layout + form UI */}
    </main>
  );
}
